'use client';

import { useEffect, useState, useRef } from 'react';
import { useRouter } from 'next/navigation';
import { doc, getDoc } from 'firebase/firestore';
import { auth, db } from '@/config/firebase';
import Sidebar from '@/components/dashboard/Sidebar';
import TopNavigation from '@/components/dashboard/TopNavigation';

interface UserData {
  role: string;
  firstName: string;
  lastName: string;
}

interface Project {
  id: string;
  title: string;
  description: string;
  faculty: string;
  department: string;
  skills: string[];
  duration: string;
  commitment: string;
}

export default function ConnectPage() {
  const router = useRouter();
  const [userData, setUserData] = useState<UserData | null>(null);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState<'discover' | 'saved'>('discover');
  const [currentProjectIndex, setCurrentProjectIndex] = useState(0);
  const [savedProjects, setSavedProjects] = useState<Project[]>([]);
  const [swipeDirection, setSwipeDirection] = useState<'left' | 'right' | 'up' | null>(null);
  const [touchStart, setTouchStart] = useState<{x: number, y: number} | null>(null);
  const [dragPosition, setDragPosition] = useState({ x: 0, y: 0 });
  const [isDragging, setIsDragging] = useState(false);
  const cardRef = useRef<HTMLDivElement>(null);

  // Sample projects data - in the future, this would come from an API with recommendation algorithm
  const projects: Project[] = [
    {
      id: '1',
      title: 'Predicting Housing Prices Using Advanced Machine Learning Models',
      description: 'This project explores the application of machine learning techniques to predict housing prices based on features such as location, square footage, and neighborhood amenities.',
      faculty: 'Dr. Jane Smith',
      department: 'Computer Science',
      skills: ['Python', 'Machine Learning', 'Data Analysis'],
      duration: '3 months',
      commitment: '10 hours/week'
    },
    {
      id: '2',
      title: 'Modeling Climate Change Impact on Regional Crop Yields',
      description: 'This project examines how climate change variables—temperature, precipitation, and CO₂ levels—affect crop yields using time-series analysis and geospatial modeling.',
      faculty: 'Dr. Michael Kim',
      department: 'Environmental Science',
      skills: ['Python', 'TensorFlow', 'Geospatial Analysis'],
      duration: '6 months',
      commitment: '15 hours/week'
    },
    {
      id: '3',
      title: 'Optimizing Traffic Flow with Reinforcement Learning Algorithms',
      description: 'This advanced research project investigates the use of reinforcement learning to optimize traffic light timings in urban areas.',
      faculty: 'Dr. Sarah Johnson',
      department: 'Civil Engineering',
      skills: ['Reinforcement Learning', 'Python', 'Simulation'],
      duration: '4 months',
      commitment: '12 hours/week'
    },
    {
      id: '4',
      title: 'Natural Language Processing for Medical Records Analysis',
      description: 'Develop NLP techniques to extract and analyze information from medical records to improve patient outcomes and healthcare efficiency.',
      faculty: 'Dr. Robert Chen',
      department: 'Health Informatics',
      skills: ['NLP', 'Python', 'Healthcare'],
      duration: '5 months',
      commitment: '8 hours/week'
    },
    {
      id: '5',
      title: 'Quantum Computing Algorithms for Optimization Problems',
      description: 'Research and implement quantum algorithms to solve complex optimization problems that are intractable for classical computers.',
      faculty: 'Dr. Lisa Patel',
      department: 'Physics',
      skills: ['Quantum Computing', 'Algorithm Design', 'Linear Algebra'],
      duration: '6 months',
      commitment: '20 hours/week'
    }
  ];

  const currentProject = projects[currentProjectIndex];

  useEffect(() => {
    const checkAuth = async () => {
      try {
        // Check if user is authenticated
        if (!auth.currentUser) {
          router.push('/development/login');
          return;
        }

        // Fetch user data from Firestore
        const userDoc = await getDoc(doc(db, 'users', auth.currentUser.uid));
        if (userDoc.exists()) {
          setUserData(userDoc.data() as UserData);
        }
      } catch (error) {
        console.error('Error fetching user data:', error);
        router.push('/development/login');
      } finally {
        setLoading(false);
      }
    };

    checkAuth();
  }, [router]);

  // Handle swipe animation end
  useEffect(() => {
    if (swipeDirection) {
      const timer = setTimeout(() => {
        setSwipeDirection(null);
        setDragPosition({ x: 0, y: 0 });
        
        if (swipeDirection === 'up') {
          // Save project
          setSavedProjects(prev => [...prev, currentProject]);
        } else if (swipeDirection === 'right') {
          // Apply to project
          // In a real app, this would trigger an application process
          alert(`Applied to project: ${currentProject.title}`);
        }
        
        // Move to next project if available
        if (currentProjectIndex < projects.length - 1) {
          setCurrentProjectIndex(prev => prev + 1);
        } else {
          // Reset to first project when we reach the end
          // In a real app with a recommendation system, you might fetch more projects here
          setCurrentProjectIndex(0);
        }
      }, 500); // Increased duration for smoother transition
      
      return () => clearTimeout(timer);
    }
  }, [swipeDirection, currentProject, currentProjectIndex, projects.length]);

  // Touch event handlers for mobile swiping
  const handleTouchStart = (e: React.TouchEvent) => {
    setIsDragging(true);
    setTouchStart({
      x: e.touches[0].clientX,
      y: e.touches[0].clientY
    });
  };

  const handleTouchMove = (e: React.TouchEvent) => {
    if (!touchStart || !isDragging) return;
    
    const currentX = e.touches[0].clientX;
    const currentY = e.touches[0].clientY;
    const deltaX = currentX - touchStart.x;
    const deltaY = currentY - touchStart.y;
    
    // Update drag position for visual feedback during drag
    setDragPosition({ x: deltaX, y: deltaY });
    
    // Increased threshold for swipe detection to prevent accidental swipes
    const swipeThreshold = 200; // Increased from 150 to 200 for even more deliberate action
    
    // Determine swipe direction based on which axis had more movement
    if (Math.abs(deltaX) > Math.abs(deltaY)) {
      if (deltaX < -swipeThreshold) {
        // Swipe left (decline)
        setSwipeDirection('left');
        setIsDragging(false);
      } else if (deltaX > swipeThreshold) {
        // Swipe right (apply)
        setSwipeDirection('right');
        setIsDragging(false);
      }
    } else {
      if (deltaY < -swipeThreshold) {
        // Swipe up (save)
        setSwipeDirection('up');
        setIsDragging(false);
      }
    }
  };

  const handleTouchEnd = () => {
    if (isDragging) {
      // Add spring-back animation when releasing before threshold
      setIsDragging(false);
      // Use a timeout to ensure the animation is visible
      setTimeout(() => {
        setDragPosition({ x: 0, y: 0 });
      }, 10);
    }
    setTouchStart(null);
  };

  // Mouse event handlers for desktop dragging
  const handleMouseDown = (e: React.MouseEvent) => {
    setIsDragging(true);
    setTouchStart({
      x: e.clientX,
      y: e.clientY
    });
  };

  const handleMouseMove = (e: React.MouseEvent) => {
    if (!touchStart || !isDragging) return;
    
    const deltaX = e.clientX - touchStart.x;
    const deltaY = e.clientY - touchStart.y;
    
    // Update drag position for visual feedback during drag
    setDragPosition({ x: deltaX, y: deltaY });
    
    // Increased threshold for swipe detection to prevent accidental swipes
    const swipeThreshold = 200; // Increased from 150 to 200 for even more deliberate action
    
    // Determine swipe direction based on which axis had more movement
    if (Math.abs(deltaX) > Math.abs(deltaY)) {
      if (deltaX < -swipeThreshold) {
        // Swipe left (decline)
        setSwipeDirection('left');
        setIsDragging(false);
      } else if (deltaX > swipeThreshold) {
        // Swipe right (apply)
        setSwipeDirection('right');
        setIsDragging(false);
      }
    } else {
      if (deltaY < -swipeThreshold) {
        // Swipe up (save)
        setSwipeDirection('up');
        setIsDragging(false);
      }
    }
  };

  const handleMouseUp = () => {
    if (isDragging) {
      // Add spring-back animation when releasing before threshold
      setIsDragging(false);
      // Use a timeout to ensure the animation is visible
      setTimeout(() => {
        setDragPosition({ x: 0, y: 0 });
      }, 10);
    }
    setTouchStart(null);
  };

  const handleDecline = () => {
    setSwipeDirection('left');
  };

  const handleSave = () => {
    setSwipeDirection('up');
  };

  const handleApply = () => {
    setSwipeDirection('right');
  };

  const removeSavedProject = (projectId: string) => {
    setSavedProjects(prev => prev.filter(project => project.id !== projectId));
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-lg">Loading...</div>
      </div>
    );
  }

  return (
    <div>
      <div className="bg-yellow-100 p-4 text-yellow-800 text-center">
        Development Environment
      </div>
      <div className="flex overflow-hidden bg-white border border-solid border-neutral-200">
        <Sidebar />
        <div className="flex flex-col grow shrink-0 self-start basis-0 w-fit max-md:max-w-full">
          <TopNavigation />
          <div className="flex flex-col items-start px-5 mt-6 w-full max-md:max-w-full">
            <div className="flex flex-wrap gap-5 justify-between self-stretch mr-6 ml-3.5 w-full font-bold text-center max-w-[1050px] max-md:mr-2.5 max-md:max-w-full">
              <div className="my-auto text-3xl text-black">Connect with Projects</div>
            </div>
            
            {/* Tabs */}
            <div className="w-full mt-6 border-b border-gray-200">
              <div className="flex">
                <button
                  onClick={() => setActiveTab('discover')}
                  className={`py-3 px-6 font-medium text-lg ${activeTab === 'discover' 
                    ? 'text-violet-800 border-b-2 border-violet-800' 
                    : 'text-gray-500 hover:text-gray-700'}`}
                >
                  Discover
                </button>
                <button
                  onClick={() => setActiveTab('saved')}
                  className={`py-3 px-6 font-medium text-lg ${activeTab === 'saved' 
                    ? 'text-violet-800 border-b-2 border-violet-800' 
                    : 'text-gray-500 hover:text-gray-700'}`}
                >
                  Saved ({savedProjects.length})
                </button>
              </div>
            </div>
            
            {/* Discover Tab Content */}
            {activeTab === 'discover' && (
              <div className="w-full mt-6 flex flex-col items-center">
                {projects.length > 0 ? (
                  <div 
                    ref={cardRef}
                    className={`w-full max-w-2xl bg-white rounded-xl shadow-md overflow-hidden relative transition-all duration-300 ease-out cursor-grab active:cursor-grabbing ${
                      swipeDirection === 'left' ? 'translate-x-[-150%] opacity-0 rotate-[-20deg]' : 
                      swipeDirection === 'right' ? 'translate-x-[150%] opacity-0 rotate-[20deg]' : 
                      swipeDirection === 'up' ? 'translate-y-[-150%] opacity-0 scale-90' : ''
                    }`}
                    style={{
                      transform: isDragging 
                        ? `translate(${dragPosition.x}px, ${dragPosition.y}px) rotate(${dragPosition.x * 0.1}deg)` 
                        : swipeDirection 
                          ? '' // Let the className handle this when swiping
                          : 'translate(0px, 0px)',
                      transition: isDragging 
                        ? 'none' 
                        : 'all 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275)' // Normal transition
                    }}
                    onTouchStart={handleTouchStart}
                    onTouchMove={handleTouchMove}
                    onTouchEnd={handleTouchEnd}
                    onMouseDown={handleMouseDown}
                    onMouseMove={handleMouseMove}
                    onMouseUp={handleMouseUp}
                    onMouseLeave={handleMouseUp}
                  >
                    {/* Progress indicators for swipe actions */}
                    <div className="absolute top-0 left-0 right-0 flex justify-between px-4 py-2">
                      <div className="h-1 w-16 bg-gray-200 rounded-full overflow-hidden">
                        <div 
                          className="h-full bg-red-500 transition-all duration-100"
                          style={{ 
                            width: dragPosition.x < 0 
                              ? `${Math.min(Math.abs(dragPosition.x) / 200 * 100, 100)}%` 
                              : '0%'
                          }}
                        ></div>
                      </div>
                      <div className="h-1 w-16 bg-gray-200 rounded-full overflow-hidden">
                        <div 
                          className="h-full bg-violet-800 transition-all duration-100"
                          style={{ 
                            width: dragPosition.x > 0 
                              ? `${Math.min(Math.abs(dragPosition.x) / 200 * 100, 100)}%` 
                              : '0%'
                          }}
                        ></div>
                      </div>
                    </div>
                    <div className="absolute top-0 left-0 right-0 flex justify-center py-2">
                      <div className="h-1 w-16 bg-gray-200 rounded-full overflow-hidden">
                        <div 
                          className="h-full bg-green-500 transition-all duration-100"
                          style={{ 
                            width: dragPosition.y < 0 
                              ? `${Math.min(Math.abs(dragPosition.y) / 200 * 100, 100)}%` 
                              : '0%'
                          }}
                        ></div>
                      </div>
                    </div>
                    
                    {/* Visual indicators for swipe direction */}
                    <div 
                      className="absolute inset-0 flex items-center justify-center bg-red-500 bg-opacity-80 rounded-xl z-0 transition-opacity duration-150"
                      style={{ 
                        opacity: dragPosition.x < -50 
                          ? Math.min(Math.abs(dragPosition.x) / 400, 0.8) 
                          : 0
                      }}
                    >
                      <div className="text-white text-2xl font-bold">DECLINE</div>
                    </div>
                    <div 
                      className="absolute inset-0 flex items-center justify-center bg-violet-800 bg-opacity-80 rounded-xl z-0 transition-opacity duration-150"
                      style={{ 
                        opacity: dragPosition.x > 50 
                          ? Math.min(Math.abs(dragPosition.x) / 400, 0.8) 
                          : 0
                      }}
                    >
                      <div className="text-white text-2xl font-bold">APPLY</div>
                    </div>
                    <div 
                      className="absolute inset-0 flex items-center justify-center bg-green-500 bg-opacity-80 rounded-xl z-0 transition-opacity duration-150"
                      style={{ 
                        opacity: dragPosition.y < -50 
                          ? Math.min(Math.abs(dragPosition.y) / 400, 0.8) 
                          : 0
                      }}
                    >
                      <div className="text-white text-2xl font-bold">SAVE</div>
                    </div>
                    
                    <div className="p-6 relative z-10 bg-white">
                      <div className="flex justify-between items-start mb-4">
                        <h2 className="text-2xl font-bold text-gray-800">{currentProject.title}</h2>
                      </div>
                      
                      <div className="mb-4">
                        <div className="text-sm text-gray-600 mb-1">Faculty: {currentProject.faculty}</div>
                        <div className="text-sm text-gray-600 mb-1">Department: {currentProject.department}</div>
                        <div className="text-sm text-gray-600 mb-1">Duration: {currentProject.duration}</div>
                        <div className="text-sm text-gray-600">Commitment: {currentProject.commitment}</div>
                      </div>
                      
                      <p className="text-gray-700 mb-6">{currentProject.description}</p>
                      
                      <div className="mb-6">
                        <div className="text-sm font-medium text-gray-700 mb-2">Skills:</div>
                        <div className="flex flex-wrap gap-2">
                          {currentProject.skills.map((skill, index) => (
                            <span 
                              key={index} 
                              className="px-3 py-1 bg-violet-100 text-violet-800 rounded-full text-sm"
                            >
                              {skill}
                            </span>
                          ))}
                        </div>
                      </div>
                      
                      <div className="flex justify-between gap-4 mt-6">
                        <button 
                          onClick={handleDecline}
                          className="flex-1 py-3 bg-gray-200 text-gray-800 rounded-lg flex items-center justify-center hover:bg-gray-300 transition"
                        >
                          <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                          </svg>
                          Decline
                        </button>
                        <button 
                          onClick={handleSave}
                          className="flex-1 py-3 bg-green-100 text-green-800 rounded-lg flex items-center justify-center hover:bg-green-200 transition"
                        >
                          <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 5a2 2 0 012-2h10a2 2 0 012 2v16l-7-3.5L5 21V5z" />
                          </svg>
                          Save
                        </button>
                        <button 
                          onClick={handleApply}
                          className="flex-1 py-3 bg-violet-800 text-white rounded-lg flex items-center justify-center hover:bg-violet-900 transition"
                        >
                          <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                          </svg>
                          Apply
                        </button>
                      </div>
                    </div>
                  </div>
                ) : (
                  <div className="text-center py-12">
                    <p className="text-xl text-gray-600">No more projects to show.</p>
                    <p className="text-gray-500 mt-2">Check back later for new opportunities!</p>
                  </div>
                )}
                
                <div className="mt-6 text-center text-gray-500">
                  <p>Swipe left to decline, right to apply, or up to save</p>
                  <p className="mt-1">Drag much further to confirm your action</p>
                  <p className="mt-1 text-sm">Or use the buttons below the card</p>
                </div>
              </div>
            )}
            
            {/* Saved Tab Content */}
            {activeTab === 'saved' && (
              <div className="w-full mt-6">
                {savedProjects.length > 0 ? (
                  <div className="grid grid-cols-1 gap-6">
                    {savedProjects.map((project) => (
                      <div key={project.id} className="bg-white rounded-xl shadow-sm overflow-hidden">
                        <div className="p-6">
                          <div className="flex justify-between items-start mb-4">
                            <h2 className="text-xl font-bold text-gray-800">{project.title}</h2>
                            <div className="flex gap-2">
                              <button 
                                onClick={() => removeSavedProject(project.id)}
                                className="p-2 text-gray-500 hover:text-gray-700 transition"
                                title="Remove from saved"
                              >
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                                </svg>
                              </button>
                            </div>
                          </div>
                          
                          <div className="mb-4">
                            <div className="text-sm text-gray-600 mb-1">Faculty: {project.faculty}</div>
                            <div className="text-sm text-gray-600 mb-1">Department: {project.department}</div>
                            <div className="text-sm text-gray-600 mb-1">Duration: {project.duration}</div>
                            <div className="text-sm text-gray-600">Commitment: {project.commitment}</div>
                          </div>
                          
                          <p className="text-gray-700 mb-6">{project.description}</p>
                          
                          <div className="mb-6">
                            <div className="text-sm font-medium text-gray-700 mb-2">Skills:</div>
                            <div className="flex flex-wrap gap-2">
                              {project.skills.map((skill, index) => (
                                <span 
                                  key={index} 
                                  className="px-3 py-1 bg-violet-100 text-violet-800 rounded-full text-sm"
                                >
                                  {skill}
                                </span>
                              ))}
                            </div>
                          </div>
                          
                          <button 
                            className="w-full py-3 bg-violet-800 text-white rounded-lg flex items-center justify-center hover:bg-violet-900 transition"
                            onClick={() => alert(`Applied to project: ${project.title}`)}
                          >
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                            </svg>
                            Apply
                          </button>
                        </div>
                      </div>
                    ))}
                  </div>
                ) : (
                  <div className="text-center py-12 bg-white rounded-xl shadow-sm">
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-16 w-16 mx-auto text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 5a2 2 0 012-2h10a2 2 0 012 2v16l-7-3.5L5 21V5z" />
                    </svg>
                    <h3 className="mt-4 text-xl font-medium text-gray-700">No saved projects yet</h3>
                    <p className="mt-2 text-gray-500">Swipe right on projects you're interested in to save them for later</p>
                    <button 
                      onClick={() => setActiveTab('discover')}
                      className="mt-6 px-6 py-2 bg-violet-800 text-white rounded-lg hover:bg-violet-900 transition"
                    >
                      Discover Projects
                    </button>
                  </div>
                )}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
} 